"use client"
import { useCategory } from "@/contexts/CategoryContext"
import { useRouter } from "next/navigation"
interface Category {
    id: number
    name: string
}

interface Props {
    product: {
        id: number
        name: string
        category: number
        price: number
        cost: number
        stock: number
    }
}

function ProductItem({ product }: Props) {
    const router = useRouter()
    const { categoryList } = useCategory()

    const categoryDisplay =
        categoryList?.find((cat: Category) => cat.id === product.category)
            ?.name || "Unknown Category"

    return (
        <tr
            onClick={() => router.push(`product/edit/${product.id}`)}
            className="cursor-pointer divide-x divide-y p-2 text-center"
        >
            <td>{product.id}</td>
            <td>{product.name}</td>
            <td>{categoryDisplay}</td>
            <td>{product.price}</td>
            <td>{product.cost}</td>
            <td>{product.stock}</td>
        </tr>
    )
}

export default ProductItem
